.statement-viewport {
  $account-viewport-base-color: #F6FAFF;
  color: #737373;
  display: flex;

  * {
    box-sizing: border-box;
  }

  .header {
    flex-direction: row !important;

    .page-header-elements {
      display: inline-flex;
      align-items: center;
      justify-content: space-around;
      flex: 1;

      .page-header-content {
        justify-content: space-around;
        flex: 1;
        padding: 0px 10px;
        color: #737373;
        display: block;

        .label-budget {
          color: #737373;
          float: left;
          margin-top: 5px;
        }

        .select-account.Select {
          display: inline-block;
          width: 280px;
          margin-left: 5px;
          float: left;

          .Select-control {
            background-color: darken(#F2F2F2, 1);
            border-color: #e6e6e6;

            .Select-value-label {
              color: #3d668f;
              font-weight: bold;
            }
          }

          &.is-focused:not(.is-open) {
            .Select-control {
              border-color: #d9d9d9;
              box-shadow: 0px 0px 0px;
            }
          }
        }

        .date-interval-picker-container {
          display: inline-block;
          float: right;

          .selected-date-range-btn {
            background-color: #EFEFEF;
            font-weight: bold;
            border-color: #e6e6e6;
            color: #3d668f;
          }
        }
      }
    }

  }

  .body {
    //flex-direction: column !important;
    display: flex;
    padding: 15px;
    justify-content: space-around;
    flex: 1;

    .statement-container {
      justify-content: space-around;
      flex: 1;
      overflow-y: auto;

      .previous-balance {
        font-size: 12px;
        font-weight: bold;
        border-bottom: solid 1px #d3e9fd;
        padding-bottom: 4px;
        color: black;
        display: flex;
        align-items: flex-end;
        flex-direction: row;
        flex: 1;

        .title {
          justify-content: space-around;
          flex: 1;
          text-align: right;
        }

        .value {
          margin-left: 10px;
          margin-right: 70px;
        }
      }

      .current-balance {
        font-size: 12px;
        font-weight: bold;
        border-top: solid 1px #d3e9fd;
        padding-top: 5px;
        color: black;
        margin-top: -1px;
        display: flex;
        align-items: flex-end;
        flex-direction: row;
        flex: 1;

        .title {
          justify-content: space-around;
          flex: 1;
          text-align: right;
        }

        .value {
          margin-left: 10px;
          margin-right: 70px;
        }
      }

      .statement {

        .statement-item {
          font-size: 12px;
          border-bottom: dotted 1px #e6e6e6;
          display: flex;
          flex-direction: row;
          align-items: center;

          .statement-item-field {
            display: inline-block;

            &.date {
              width: 120px;
              text-align: center;
              margin-left: 300px;
            }

            &.description {
              width: 250px;
            }

            &.value {
              width: 120px;
              text-align: right;
            }

            &.type {
              margin-left: 4px;
            }

            &.details {
              margin-left: 8px;
              cursor: pointer;

              svg {
                margin-top: -2px;
              }
            }

          }

        }
      }
    }

  }

}



